export default {
	data() {
		return {
			imgList: [],
			type: '',
			name: '',
			price: '',
			unit: ''
		}
	},
	onLoad(e) {
		this.type = this.$route.query.type
		this.chooseImage() // 打开页面默认打开相机
	},
	methods: {
		chooseImage() { // 拍照
			if (!this.imgList.length) {
				uni.chooseImage({ sourceType: ['camera'], sizeType: ['compressed'] }).then(([, { tempFilePaths }]) => this.imgList = tempFilePaths)
			}
		},
		viewImage(e) { // 预览图片
			uni.previewImage({ urls: this.imgList })
		},
		delImg(e) { // 删除图片
			uni.showModal({
				title: '删除',
				content: '确定删除这张商品图片嘛？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		async addGood() { // 添加商品
			if (!this.imgList.length) {
				uni.showToast({ title: '商品图片不能为空', icon: 'none' })
				return
			}
			if (!this.name) {
				uni.showToast({ title: '商品名称不能为空', icon: 'none' })
				return
			}
			if (!this.price) {
				uni.showToast({ title: '商品价格不能为空', icon: 'none' })
				return
			}
			if (!this.unit) {
				uni.showToast({ title: '商品单位不能为空', icon: 'none' })
				return
			}
			uni.showLoading({ mask:true, title: '添加中...' })
			const { fileID: _url } = await uniCloud.uploadFile({ filePath: this.imgList[0] }) // 上传原图
			const filePath = await this.$mini(this.imgList[0]) // 压缩图片
			const { fileID: url } = await uniCloud.uploadFile({ filePath }) // 上传压缩原图
			uniCloud.callFunction({
				name: 'good-C',
				data: {
					url,
					_url,
					type: this.type,
					unit: this.unit,
					name: this.name,
					price: this.price,
					addTime: Date.now()
				},
			}).then(({ result }) => {
				uni.hideLoading()
				uni.showModal({
					title: '添加成功',
					showCancel: false,
					content: `${this.name}    ${this.price}元 / ${this.unit}`
				}).then(() => {
					uni.$emit(`REFRESH${this.type.split('-')[0]}`)
					this.$router.pop()
				})
			}).catch(e => {
				uni.hideLoading()
				uni.showToast({ title: '上传失败，请联系成诺', icon: 'none' })
			})
		}
	}
}
